<template>
  <div id="mainview">
    <h1>Member Sekolah Koding</h1>

    <div id="presidents">
      <div v-if="singleview">
        <president class="each-president" :president="active_president"></president>
        <!-- edit president -->
        <input type="text" v-model="active_president.name" placeholder="name"> <br>
        <input type="text" v-model="active_president.img" placeholder="avatar">  <br>
        <input type="text" v-model="active_president.order_id" placeholder="point">
      </div>

      <div v-else>
        <president class="each-president" v-for="president in presidents" :president="president"
            v-if="president.img && president.name && president.order_id"
        ></president>

      </div>
    </div>
  </div>
</template>

<script>
import President from './President'
import { mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters([
      'presidents',
      'singleview',
      'active_president',
    ])
  },
  components: {
    President
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../assets/scss/variables';
  #mainview{
    flex: 3;
    padding: 0 5% 5%;
  }
  h1 {
    color: $soft-red;
  }
  input{
    border: 0px;
    border-bottom: 2px solid grey;
    font-size: 20px;
    margin-bottom: 5px;
    &:hover {
      border-bottom: 2px solid rgb(96, 94, 97);
    }
    &:focus {
      outline: none;
    }
  }
</style>



