import * as mutations from './mutations-types'

export default {
    showPresidents (context) {
        context.commit(mutations.SHOW_PRESIDENTS)
    },
    showPresident (context, president) {
        context.commit(mutations.SHOW_PRESIDENT, president)
    },
    addPresident (context) {
        context.commit(mutations.ADD_PRESIDENT)
    },
    removePresident (context, id) {
        context.commit(mutations.REMOVE_PRESIDENT, id)
        context.dispatch('showPresidents')
    },

    //mutaitonr emove
    // [mutations.REMOVE_PRESIDENT] (state, id) {
    //     state.presidents = state.presidents.filter((president) => {
    //         return president.id !== id
    //     })
    // },
}



