<template>
  <div id="sidebar">
    <div class="presidents">
      <h2>Daftar User</h2>

      <div class="each-president" v-for="president in presidents"
          v-if="president.img && president.name && president.order_id"
          v-bind:class="{ active: president == active_president }">

        <div @click.prevent="showPresident(president)">
          <h3>{{ (president.name).substring(0, 8) }} <br>#{{ president.order_id }} </h3>
          <img :src="'/static/images/' + president.img" width="200" v-if="president.img">
        </div>

        <a href="#" class="btn btn-remove" @click.prevent="removePresident(president.id)">X</a>
      </div>
    </div>

    <div id="actions">
      <a href="#" class='btn btn-blue' @click.prevent="showPresidents()">Lihat Semuanya</a>
      <a href="#" class='btn btn-blue' @click.prevent="addPresident()">Tambah Member</a>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  computed: {
    ...mapGetters([
      'presidents',
      'active_president'
    ])
  },
  methods: {
    ...mapActions([
      'showPresidents',
      'showPresident',
      'addPresident',
      'removePresident'
    ])
  },
  filters: {
    truncate: function (string, value) {
        return string.substring(0, value) + '...';
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../assets/scss/variables';
  .active {
    transition: border-right 0.1s ease-in;
    border-right: 10px solid $soft-red;
  }
  #sidebar {
    flex: 1;
  }
  .btn {
    font-size: 20px;
    width: 100%;
    display: block;
    color: white;
  }
  .btn-blue {
    background: rgb(71, 173, 223);
    &:hover {
      cursor: pointer;
      background: rgb(52, 125, 222);
    }
  }
  .btn-remove {
    background: $soft-red;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    position: absolute;
    top: -30px;
    right: 0px;
    transition: top 0.2s ease-in;
    &:hover {
      background: $softer-red;
    }
  }
  .presidents {
    h2 {
      background: $soft-red;
      color: white;
    }
  }
  .each-president {
    position: relative;
    overflow: hidden;
    div{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
    }
    margin-bottom: 10px;
    padding: 5px 2px;
    img {
      margin-left: 5px;
      border-radius: 50%;
      width: 100px;
      height: 100px;
    }
    &:hover {
      background: rgb(212, 223, 218);
      cursor: pointer;
      > .btn-remove {
        top: 10px;
      }
    }
  }
  #actions {
    a {
      margin-bottom: 5px;
    }
  }
</style>



