import * as mutations from './mutations-types'

export default {
    [mutations.SHOW_PRESIDENTS] (state) {
        state.singleview = false
        state.active_president = null
    },

    [mutations.SHOW_PRESIDENT] (state, president) {
        state.singleview = true
        state.active_president = president
    },

    [mutations.ADD_PRESIDENT] (state) {
        state.singleview = true

        var empty_president = {
            id: Date.now(),
            name: '',
            order_id: null,
            img : '',
        }

        state.active_president = empty_president
        state.presidents.push(empty_president)
    },

    [mutations.REMOVE_PRESIDENT] (state, id) {
        state.presidents = state.presidents.filter((president) => {
            return president.id !== id
        })
    },

}



